.BasketProductCard {
	display: flex;
	align-items: center;
	justify-content: space-between;

	width: 90%;
	height: 175px;

	border: solid 1px rgb(192, 192, 192);
	border-radius: 20px;

	.card-left {
		display: flex;
		align-items: center;

		height: 100%;
		margin-left: 25px;
		margin-right: 35px;
	}

	.product-img-basket {
		width: 145px;
		height: 145px;
		margin-right: 15px;
	}

	.name-count-basket {
		display: flex;
		flex-direction: column;
		justify-content: space-between;

		height: 100%;

		.name-count-basket-empty {
			height: 33px;
		}
	}

	.prod-name-basket {
		margin-top: 13px;
	}

	.add-count {
		width: 50px;
		min-width: 50px;
		height: 33px;

		margin-bottom: 15px;

		border: solid 1px rgb(192, 192, 192);
		border-radius: 20px;

		text-align: center;
		font-weight: 500;
	}

	.card-right {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;

		width: 65px;
		height: 136px;
		margin-right: 25px;
	}

	.prices {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;

		text-align: right;
		height: 47px;

		.prod-price-basket {
			margin-top: 4px;
			font-size: 22px;
			font-weight: 500;
		}

		.base-price {
			margin-right: 1px;
		}
	}

	.remove-item {
		display: flex;
		justify-content: flex-end;

		width: 100%;
		cursor: pointer;
	}

	.card-right-empty {
		height: 25px;
	}
}
