$light-gray: rgb(134, 134, 134);

.Filters {
	display: flex;
	flex-direction: column;
	gap: 40px;

	padding: 30px 30px 30px 40px;
	border-right: 1px solid $light-gray;

	background-color: #f3f3f3;
}

.reset-btn-container {
	display: flex;
	justify-content: flex-end;

	margin-top: -20px;
	margin-right: -20px;
	margin-bottom: -30px;
}

#reset-filters-btn {
	height: 23px;
	width: 90px;

	background-color: white;
	border: rgb(196, 194, 194) solid 1px;
	border-radius: 3px;

	&:active {
		background-color: rgb(221, 221, 221);
	}
}

.filter {
	display: flex;
	flex-direction: column;
	gap: 8px;
}

.filter-title {
	font-size: 19px;
	font-weight: 500;
	margin-bottom: 5px;
}

.all-cat {
	margin-left: 25px;
}

.all-cat {
	font-size: 17px;

	cursor: pointer;
}

.sub-cat {
	margin-left: 37px;
	cursor: pointer;
}

.price-range {
	display: flex;
	gap: 10px;
}

input[type='number'] {
	height: 30px;
	width: 50px;

	font-size: 15px;

	text-align: center;
	border: solid $light-gray 1px;
	border-radius: 6px;
}

.price-range-text {
	margin-top: 7px;
	font-size: 17px;
}

.availability {
	display: flex;
	align-items: center;
}

#in-stock-checkbox {
	transform: scale(1.3);
}

.in-stock {
	margin-left: 7px;
}

.fat-text {
	font-weight: 600;
}

// Disable arrows in input type number
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

/* Firefox */
input[type='number'] {
	-moz-appearance: textfield;
}
